
import React, { useRef, useMemo, useEffect, useState, useCallback } from 'react';
import { AntDesign, MaterialCommunityIcons, Feather, Ionicons } from '@expo/vector-icons';
import { Dimensions, StyleSheet, View, TouchableOpacity, Platform, Text, FlatList } from 'react-native';
import { Gesture, GestureDetector, GestureHandlerRootView } from 'react-native-gesture-handler';
import Animated, { useAnimatedStyle, useSharedValue, withSpring, } from 'react-native-reanimated';
import theme from '../assets/theme';
import moment from 'moment';

const platformTiles = Platform.OS === 'web' ? 8 : 6;

const doTranslateTableColor = (item) => {
  if (!item) return theme.grey9;
  if (item.adhocItem) return theme.adhoc;
  if (item.transType !== 'A') return theme.deposit;
  if (item.statusFlg === 'A') {
    return theme.vacant;
  } else if (item.statusFlg === 'B') {
    return theme.reserved;
  } else if (item.statusFlg === 'C') {
    return theme.occupied;
  } else if (item.statusFlg === 'D') {
    return theme.paid;
  } else if (item.statusFlg === 'E') {
    return theme.unavailable;
  }
}

export default function MainTableFloor({ refresh, tables, handleUpdateFromMainTableFloor }) {
  const translateX = useSharedValue(0);
  const translateY = useSharedValue(0);
  const scale = useSharedValue(1);
  const context = useSharedValue({ x: 0, y: 0, scale: 1 });

  const tileSize = 40;
  const floorTables = tables.filter(el => !(el.xaxis === 0 && el.yaxis === 0));
  const infoTables = floorTables.filter(el => el.statusFlg === 'C' || el.statusFlg === 'D');
  const tempTables = tables.filter(el => el.xaxis === 0 && el.yaxis === 0);

  const minX = floorTables.reduce((min, el) => Math.min(el.xaxis, min), floorTables[0]?.xaxis || 0);
  const maxX = floorTables.reduce((max, el) => Math.max(el.xaxis + el.width, max), (floorTables[0]?.xaxis + floorTables[0]?.width) || 0);

  const minY = floorTables.reduce((min, el) => Math.min(el.yaxis, min), floorTables[0]?.yaxis || 0);
  const maxY = floorTables.reduce((max, el) => Math.max(el.yaxis + el.height, max), (floorTables[0]?.yaxis + floorTables[0]?.width) || 0);

  // const floorWidth = Math.max((maxX - minX) * tileSize, 16 * tileSize);
  const floorWidth = (maxX - minX) * tileSize;
  const floorHeight = (maxY - minY) * tileSize;

  const [focused, setFocused] = useState(null);

  useEffect(() => {
    setFocused(null);
  }, [refresh]);

  const doPressTable = (item, focused) => {
    if (item.recKey === focused?.recKey) {
      handleUpdateFromMainTableFloor(item, true);
      setFocused(null);
    } else {
      handleUpdateFromMainTableFloor(item, false);
      setFocused(item);
    }
  }

  // console.log(minX, maxX, minY, maxY, floorTables, tables, floorWidth);

  const gesturePinch = Gesture.Pinch()
    .onStart(() => {
      context.value = { ...context.value, scale: scale.value };
    })
    .onUpdate((event) => {
      scale.value = context.value.scale * event.scale;
    })

  const gesturePan = Gesture.Pan()
    .onStart(() => {
      context.value = { ...context.value, x: translateX.value, y: translateY.value };
    })
    .onUpdate((event) => {
      translateX.value = event.translationX + context.value.x;
      translateY.value = event.translationY + context.value.y;
    })

  const gesture = Gesture.Simultaneous(gesturePinch, gesturePan);

  const rStyle = useAnimatedStyle(() => {
    return {
      transform: [{ translateX: translateX.value }, { translateY: translateY.value }, { scale: scale.value }]
    }
  })

  // return;
  return (
    <GestureHandlerRootView style={{ flex: 1 }}>
      <GestureDetector gesture={gesture}>
        <View style={{ flex: 1, backgroundColor: 'rgba(0,0,0,0.9)', justifyContent: 'center', alignItems: 'center' }}>
          <Animated.View style={[rStyle, { height: floorHeight + Math.ceil(tempTables.length / platformTiles) * 2 * tileSize, alignItems: 'center', }]}>
            <View style={{ height: floorHeight, width: floorWidth, }}>
              {
                floorTables.map(item =>
                  <View key={item.recKey}
                    style={{ position: 'absolute', left: (item.xaxis - minX) * tileSize, top: Number((item.yaxis - minY) * tileSize), width: item.width * tileSize, height: Number(item.height * tileSize) }}>
                    {
                      // top and bottom chairs
                      (item.width > item.height && item.shape === 'A') &&
                      <>
                        <View style={{ position: 'absolute', top: -1, left: 0, right: 0, height: 2, flexDirection: 'row', }}>
                          {
                            [...Array(item.width)].map((el, index) =>
                              <View key={index} style={{ flex: 1 / item.width, marginHorizontal: 10, backgroundColor: 'white', borderRadius: 2, }}>

                              </View>)
                          }
                        </View>
                        <View style={{ position: 'absolute', bottom: -1, left: 0, right: 0, height: 2, flexDirection: 'row', }}>
                          {
                            [...Array(item.width)].map((el, index) =>
                              <View key={index} style={{ flex: 1 / item.width, marginHorizontal: 10, backgroundColor: 'white', borderRadius: 2, }}>

                              </View>)
                          }
                        </View>
                      </>
                    }
                    {
                      // left and right chairs
                      (item.width < item.height && item.shape === 'A') &&
                      <>
                        <View style={{ position: 'absolute', left: -1, top: 0, bottom: 0, width: 2, }}>
                          {
                            [...Array(item.height)].map((el, index) =>
                              <View key={index} style={{ flex: 1 / item.height, marginVertical: 10, backgroundColor: 'white', borderRadius: 2, }}>

                              </View>)
                          }
                        </View>
                        <View style={{ position: 'absolute', right: -1, top: 0, bottom: 0, width: 2, }}>
                          {
                            [...Array(item.height)].map((el, index) =>
                              <View key={index} style={{ flex: 1 / item.height, marginVertical: 10, backgroundColor: 'white', borderRadius: 2, }}>

                              </View>)
                          }
                        </View>
                      </>
                    }
                    {
                      item.shape === 'B' ?
                        <TouchableOpacity style={{ flex: 1, alignItems: 'center', justifyContent: 'center', borderRadius: item.shape === 'C' ? tileSize * item.height : 0 }}
                          onPress={() => doPressTable(item, focused)}>
                          <View style={{ height: Math.sqrt(0.5) * (item.width * tileSize - 6), width: Math.sqrt(0.5) * (item.width * tileSize - 6), backgroundColor: focused?.recKey === item.recKey ? 'white' : doTranslateTableColor(item), transform: [{ rotateZ: '45deg' }], justifyContent: 'center', alignItems: 'center', borderRadius: 3 }}>
                          </View>
                          <View style={{ position: 'absolute', top: 3, bottom: 3, left: 3, right: 3, justifyContent: 'center', alignItems: 'center' }}>
                            <Text style={{ fontFamily: 'varela-round', color: theme.black8, fontSize: 14 }}>{item.tableId}</Text>
                            {
                              item.vipName?.length > 0 &&
                              <Text style={{ paddingHorizontal: 3, textAlign: 'center', fontFamily: 'varela-round', color: focused?.recKey === item.recKey ? theme.black8 : 'white', fontSize: 10, textTransform: 'uppercase' }} numberOfLines={1}>{item.vipName.split(' ')[0]}</Text>
                            }
                          </View>
                        </TouchableOpacity>
                        :
                        <TouchableOpacity style={{ flex: 1, margin: 3, backgroundColor: focused?.recKey === item.recKey ? 'white' : doTranslateTableColor(item), alignItems: 'center', justifyContent: 'center', borderRadius: item.shape === 'C' ? tileSize * item.height : 3, }}
                          onPress={() => doPressTable(item, focused)}>
                          <Text style={{ fontFamily: 'varela-round', color: theme.black8, fontSize: 14 }}>{item.tableId}</Text>
                          {
                            item.vipName?.length > 0 &&
                            <Text style={{ paddingHorizontal: 3, textAlign: 'center', fontFamily: 'varela-round', color: focused?.recKey === item.recKey ? theme.black8 : 'white', fontSize: 10, textTransform: 'uppercase' }} numberOfLines={1}>{item.vipName.split(' ')[0]}</Text>
                          }
                        </TouchableOpacity>
                    }
                  </View>
                )
              }
              {
                infoTables.map(item =>
                  <View key={item.recKey}
                    pointerEvents='none'
                    style={{ position: 'absolute', left: (item.xaxis - minX) * tileSize, top: Number((item.yaxis - minY) * tileSize) }}>
                    <View style={{ borderLeftWidth: 3, borderLeftColor: focused?.recKey === item.recKey ? 'white' : doTranslateTableColor(item), position: 'absolute', top: item.shape === 'B' ? 0 : item.shape === 'C' ? -7 : -12, left: item.shape === 'B' ? 0 : item.shape === 'C' ? -7 : -12, backgroundColor: 'rgba(0,0,0,0.4)', padding: 3, borderRadius: 3, minWidth: 38 }}>
                      <Text style={{ fontFamily: 'varela-round', color: 'white', fontSize: 10, }}>{item.seatTime && moment(item.seatTime).format('HH:mm')}</Text>
                      <Text style={{ fontFamily: 'varela-round', color: 'white', fontSize: 10, }}><Feather color='white' name='users' size={10} /> {item.pax}</Text>
                    </View>
                  </View>
                )
              }
            </View>
            <FlatList
              style={{ flexGrow: 0, overflow: 'visible', width: tempTables.length > (platformTiles - 1) ? 2 * platformTiles * tileSize : 2 * tempTables.length * tileSize, }}
              numColumns={platformTiles}
              data={tempTables}
              showsVerticalScrollIndicator={false}
              scrollEnabled={false}
              keyExtractor={(item, index) => index.toString()}
              renderItem={({ item }) =>
                <View style={{ width: 2 * tileSize, height: 2 * tileSize }}>
                  <TouchableOpacity style={{ flex: 1, margin: 3, backgroundColor: focused?.recKey === item.recKey ? 'white' : doTranslateTableColor(item), alignItems: 'center', justifyContent: 'center', borderRadius: 3, }}
                    onPress={() => doPressTable(item, focused)}>
                    {
                      item.openorderRecKey &&
                      <Text style={{ paddingHorizontal: 3, textAlign: 'center', fontFamily: 'varela-round', color: focused?.recKey === item.recKey ? theme.black8 : 'white', fontSize: 10, textTransform: 'uppercase' }} numberOfLines={1}>{item.openorderRecKey}</Text>
                    }
                    <Text style={{ fontFamily: 'varela-round', color: theme.black8, fontSize: 14, }}>{item.tableId}</Text>
                    {
                      item.vipName?.length > 0 &&
                      <Text style={{ paddingHorizontal: 3, textAlign: 'center', fontFamily: 'varela-round', color: focused?.recKey === item.recKey ? theme.black8 : 'white', fontSize: 10, textTransform: 'uppercase' }} numberOfLines={1}>{item.vipName.split(' ')[0]}</Text>
                    }
                  </TouchableOpacity>
                  {
                    (item.statusFlg === 'C' || item.statusFlg === 'D') &&
                    <View pointerEvents='none'
                      style={{ borderLeftWidth: 3, borderLeftColor: focused?.recKey === item.recKey ? 'white' : doTranslateTableColor(item), position: 'absolute', top: item.shape === 'B' ? 0 : item.shape === 'C' ? -7 : -12, left: item.shape === 'B' ? 0 : item.shape === 'C' ? -7 : -12, backgroundColor: 'rgba(0,0,0,0.4)', padding: 3, borderRadius: 3, minWidth: 38 }}>
                      <Text style={{ fontFamily: 'varela-round', color: 'white', fontSize: 10, }}>{item.seatTime && moment(item.seatTime).format('HH:mm')}</Text>
                      <Text style={{ fontFamily: 'varela-round', color: 'white', fontSize: 10, }}><Feather color='white' name='users' size={10} /> {item.pax}</Text>
                    </View>
                  }
                </View>
              }
            />
          </Animated.View>
        </View>
      </GestureDetector>
    </GestureHandlerRootView>
  );
}

const styles = StyleSheet.create({
  container: {
    width: 400, height: 400,
    backgroundColor: '#fff',
    overflow: 'hidden',
  },
});