import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions,
  FlatList,
} from 'react-native';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import theme from '../assets/theme';
import { debug } from '../assets/utility';

export default class SplitTable extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      tableItems: props.route.params.tableItems,
      selectedTable: props.route.params.selectedTable,

      settings: props.route.params.masters.settings,

      selectedTableItems: [],

      overlayProcedureMessageVisible: false,
    };
    this.handlePressSelectAll = this.handlePressSelectAll.bind(this);
    this.handleSelectTableItem = this.handleSelectTableItem.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
  }

  generateTextForSelectAll() {
    const { selectedTableItems } = this.state;
    if (selectedTableItems.length === 0) {
      return 'SELECT ALL';
    } else {
      return 'DESELECT ALL';
    }
  }

  handlePressSelectAll() {
    const { selectedTableItems, tableItems } = this.state;

    if (selectedTableItems.length === 0) {
      this.setState({
        selectedTableItems: tableItems.map(el => ({
          ...el,
          editOrderQty: el.orderQty,
        })),
      })
    } else {
      this.setState({
        selectedTableItems: [],
      })
    }
  }

  handleSelectTableItem(item) {
    console.log('tableitem selected', item);
    const { selectedTableItems, tableItems } = this.state;

    let selectedTableItemsUpdate = selectedTableItems;

    if (!selectedTableItems.some(el => el.recKey === item.recKey)) {
      selectedTableItemsUpdate.push({
        ...item,
        editOrderQty: item.orderQty,
      });
      let comboItems = [];
      comboItems = tableItems.filter(el => el.refRecKey === item.recKey);
      console.log('comboItems', comboItems);
      comboItems.forEach(el => selectedTableItemsUpdate.push(el));
    } else {
      selectedTableItemsUpdate = selectedTableItemsUpdate.filter(el => el.recKey !== item.recKey && el.refRecKey !== item.recKey);
    }
    this.setState({
      selectedTableItems: selectedTableItemsUpdate,
    });
  }

  doChangeQty(type, item) {
    const { selectedTableItems, tableItems } = this.state;
    let selectedTableItemsUpdate = selectedTableItems;
    let foundSelected = selectedTableItems.find(el => el.recKey === item.recKey);
    let foundOriginal = tableItems.find(el => el.recKey === item.recKey);

    if (type === 'PLUS') {
      if (!selectedTableItems.some(el => el.recKey === item.recKey)) {
        selectedTableItemsUpdate.push({
          ...foundOriginal,
          editOrderQty: foundOriginal.orderQty,
        });
        let comboItems = [];
        comboItems = tableItems.filter(el => el.refRecKey === item.recKey);
        // console.log('comboItems', comboItems);
        comboItems.forEach(el => selectedTableItemsUpdate.push(el));
      } else {
        if (Number(foundSelected.editOrderQty) < Number(foundOriginal.orderQty)) {
          selectedTableItemsUpdate.find(el => el.recKey === item.recKey)['editOrderQty'] = Number(foundSelected.editOrderQty) + 1;
        }
      }

    } else if (type === 'MINUS') {
      if (!selectedTableItems.some(el => el.recKey === item.recKey)) {
        // do nothing
      } else {
        console.log(foundSelected.editOrderQty, foundOriginal.orderQty)
        if (Number(foundSelected.editOrderQty) > 1) {
          selectedTableItemsUpdate.find(el => el.recKey === item.recKey)['editOrderQty'] = Number(foundSelected.editOrderQty) - 1;
        }
      }
    }

    this.setState({ selectedTableItems: selectedTableItemsUpdate })
  }

  handleSubmit() {
    const { navigation } = this.props;
    const { selectedTable, selectedTableItems, home, serviceEntry, settings } = this.state;

    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/split-table';
    const body = {
      userId: home.userId,
      locId: home.locId,
      itemPayloads: selectedTableItems.filter(el => !el.refRecKey).map(el => ({
        opentableRecKey: selectedTable.recKey,
        opentableItemRecKey: el.recKey,
        splitOrderQty: el.editOrderQty,
      }))
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post split-table successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Split Table',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            response.opentable?.tableId,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        this.props.route.params.handleUpdateFromSplitTable(response)
        navigation.goBack();
      })
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  render() {
    const { navigation, route } = this.props;
    const { tableItems, home, selectedTable, selectedTableItems,
      overlayProcedureMessageVisible } = this.state;
    console.log('in splittable');

    return (
      <View style={styles.container}>
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => navigation.goBack()}>
                <AntDesign
                  name='left'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('back')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
        <View style={[styles.displayContainer, { flex: 6 }]}>
          <View style={[styles.displayWrapper]}>
            <View style={styles.overlayHeader}>
              <Text style={styles.overlayTitleText}>{intl.get('table')} {selectedTable?.tableId || '-'}</Text>
              <Text style={styles.overlaySubtitleText}>{intl.get('splittable')}</Text>
            </View>
            <View style={styles.gap}></View>
            <View style={styles.gridContainer}>
              <View style={[styles.searchBox, { backgroundColor: theme.grey3, alignItems: 'center', justifyContent: 'center' }]}>
                <TouchableOpacity
                  onPress={() => this.handlePressSelectAll()}>
                  <Text style={styles.titleText}>{this.generateTextForSelectAll()}</Text>
                </TouchableOpacity>
              </View>
              <View style={styles.searchBoxInfo}>
                <View style={[styles.titleFirstContainer, { width: 120 }]}>
                  <Text style={styles.titleText}>Qty</Text>
                </View>
                <View style={styles.titleSecondContainer}>
                  <Text style={styles.titleText}>Desc</Text>
                </View>
                {/* <View style={styles.titleThirdContainer}>
                    <Text style={styles.titleText}>Price</Text>
                  </View> */}
                <View style={styles.titleFourthContainer}>
                  <Text style={styles.titleText}>Price</Text>
                </View>
              </View>
              <FlatList
                style={{ flex: 1 }}
                showsVerticalScrollIndicator={false}
                extraData={this.state}
                data={tableItems}
                keyExtractor={item => item.recKey.toString()}
                renderItem={({ item }) => (
                  <TouchableOpacity
                    style={[styles.tableItemContainer, selectedTableItems.some(el => el.recKey === item.recKey) && { backgroundColor: theme.occupied }, item.refRecKey && { opacity: 0.2 }]}
                    disabled={item.refRecKey ? true : false}
                    onPress={() => this.handleSelectTableItem(item)}>
                    <View style={[styles.tableItemFirstContainer, { width: 120, flexDirection: 'row' }]}>
                      <TouchableOpacity style={{ flex: 1, height: '100%', justifyContent: 'center', alignItems: 'center' }}
                        disabled={item.menuType === 'X'}
                        onPress={() => this.doChangeQty('MINUS', item)}>
                        <AntDesign name='minus' size={16} color='white' />
                      </TouchableOpacity>
                      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
                        <Text style={styles.tableItemText}>{selectedTableItems.find(el => el.recKey === item.recKey)?.editOrderQty || '0'}</Text>
                      </View>
                      <TouchableOpacity style={{ flex: 1, height: '100%', justifyContent: 'center', alignItems: 'center' }}
                        disabled={item.menuType === 'X'}
                        onPress={() => this.doChangeQty('PLUS', item)}>
                        <AntDesign name='plus' size={16} color='white' />
                      </TouchableOpacity>
                    </View>
                    <View style={styles.tableItemSecondContainer}>
                      <Text style={styles.tableItemText}>
                        {item.refRecKey ? '- ' : null}{item.menuName}
                      </Text>
                      {
                        item.menuMod &&
                        <Text style={styles.tableItemModText}>{item.menuMod}</Text>
                      }
                    </View>
                    {/* <View style={styles.tableItemThirdContainer}>
                        <Text style={styles.tableItemText}>
                          {item.netPrice && item.netPrice.toFixed(2)}
                        </Text>
                      </View> */}
                    <View style={styles.tableItemFourthContainer}>
                      <Text style={styles.tableItemText}>
                        {item.netPrice && item.netPrice.toFixed(2)}
                      </Text>
                    </View>
                  </TouchableOpacity>
                )}
              />
            </View>
            <View style={styles.gap}></View>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={[styles.button]}
                onPress={() => this.handleSubmit()}>
                <AntDesign name='menuunfold' color='white' size={36} />
                <Text style={styles.buttonText}>{intl.get('splittable')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

