import { Component } from 'react';
import { AntDesign, MaterialCommunityIcons, Feather, Ionicons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Image, Text, TextInput, Alert, FlatList, ScrollView, Platform } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { } from '../assets/utility';
import theme from '../assets/theme';
import intl from 'react-intl-universal';
import moment from 'moment';
import MainTableFloor from './MainTableFloor';

export default class MainTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      home: props.home,
      serviceEntry: props.serviceEntry,
      preference: props.preference,
      settings: props.masters.settings,
      sections: props.masters.sections || [],
      tablemas: props.masters.tablemas,
      tables: [],

      selectedSection: null,

      floor: true,
    };

    this.handleUpdateFromMainTableFloor = this.handleUpdateFromMainTableFloor.bind(this);
  }

  async componentDidMount() {
    await this.doRefresh(true);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.refresh && prevProps.refresh !== this.props.refresh) {
      console.log('componentDidUpdate', prevProps.refresh, this.props.refresh)
      this.doRefresh();
    }
  }

  async doRefresh(initial) {
    const { sections, selectedSection } = this.state;
    if (initial) {
      await this.doPressFloor();
    } else {
      await this.doPressSection(selectedSection);
    }
  }

  async doPressFloor() {
    const { serviceEntry, home } = this.state;
    let url = serviceEntry + 'api/tables';
    const params = new URLSearchParams();
    params.append('shopId', home.shopId);

    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        this.setState({
          tables: response,
          selectedSection: null,
        });
      })
  }

  async doPressSection(item) {
    const { serviceEntry, home } = this.state;

    if (!item) {
      this.doPressFloor();
      return;
    }

    let url = serviceEntry + 'api/tables';
    const params = new URLSearchParams();
    params.append('shopId', home.shopId);

    if (item?.sectionId === 'OCCUPIED') {
      params.append('statusFlg', 'C');
    } else if (item?.sectionId === 'VACANT') {
      params.append('statusFlg', 'A');
    } else if (item?.sectionId === 'RESERVED') {
      params.append('statusFlg', 'B');
    } else if (item?.sectionId === 'PAID') {
      params.append('statusFlg', 'D');
    } else {
      params.append('sectionId', item.sectionId)
    }

    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        this.setState({
          tables: response,
          selectedSection: { sectionId: item.sectionId },
        });
      })
  }

  handleUpdateFromMainTableFloor(item, focused) {
    const { tables, selectedSection } = this.state;
    if (focused) {
      this.doPressFocusedTable(item);
      return;
    }
    this.doPressSection(selectedSection)
    this.props.handlePressTable(item);
  }

  doPressFocusedTable(item) {
    if (item.statusFlg === 'A') {
      this.props.handleActionTable(item, 'OPEN')
    } else if (item.statusFlg === 'C') {
      this.props.handleActionTable(item, 'EDIT')
    }
  }

  render() {
    const { preference, sections, settings, tables, selectedSection, floor } = this.state;

    return (
      <View style={{ flex: 1, flexDirection: 'row', backgroundColor: theme.grey1, padding: 5 }}>
        <View style={{ flex: 1, margin: 5, backgroundColor: theme.grey2, borderRadius: 5, overflow: 'hidden' }}>
          <MainTableFloor
            key={selectedSection?.sectionId || 'FLOOR'}
            refresh={this.props.refresh}
            tables={tables}
            handleUpdateFromMainTableFloor={this.handleUpdateFromMainTableFloor}
          />
          {
            (selectedSection && (selectedSection?.sectionId !== 'PAID' && selectedSection?.sectionId !== 'RESERVED' && selectedSection?.sectionId !== 'OCCUPIED')) &&
            <TouchableOpacity style={{ position: 'absolute', right: 0, top: 0, margin: 10, height: 100, width: 50, borderRadius: 5, backgroundColor: theme.grey1, alignItems: 'center', justifyContent: 'center', }}
              onPress={() => this.props.handleActionTable(selectedSection, 'CREATE')}>
              <MaterialCommunityIcons name="apps-box" size={28} color='white' style={{ height: 28, width: 28 }} />
              <Text style={{ marginTop: 10, fontFamily: 'varela-round', fontSize: 10, color: 'white', textTransform: 'uppercase' }}>{intl.get('new')}</Text>
            </TouchableOpacity>
          }
        </View>
        <View style={{ marginVertical: 5, width: 130, }}>
          <TouchableOpacity
            style={{ width: 120, alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 80, borderRadius: 5, margin: 5 }}
            onPress={() => this.doPressFloor()}>
            <MaterialCommunityIcons name="mapbox" size={30} color="white" style={{ height: 28 }} />
            <Text style={{ marginTop: 10, fontFamily: 'varela-round', color: 'white', fontSize: 10, textTransform: 'uppercase' }}>{intl.get('all')}</Text>
          </TouchableOpacity>
          <View style={{ flexDirection: 'row', justifyContent: 'space-between', paddingHorizontal: 0, height: 50, width: 120, backgroundColor: 'none', margin: 5 }}>
            <TouchableOpacity style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => this.doPressSection({ sectionId: 'PAID' })}>
              <View style={{ height: 27, width: 27, borderRadius: 14, backgroundColor: selectedSection?.sectionId === 'PAID' ? 'white' : theme.paid, }}>

              </View>
            </TouchableOpacity>
            <TouchableOpacity style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => this.doPressSection({ sectionId: 'RESERVED' })}>
              <View style={{ height: 27, width: 27, borderRadius: 14, backgroundColor: selectedSection?.sectionId === 'RESERVED' ? 'white' : theme.reserved, }}>

              </View>
            </TouchableOpacity>
            <TouchableOpacity style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => this.doPressSection({ sectionId: 'OCCUPIED' })}>
              <View style={{ height: 27, width: 27, borderRadius: 14, backgroundColor: selectedSection?.sectionId === 'OCCUPIED' ? 'white' : theme.occupied, }}>

              </View>
            </TouchableOpacity>
          </View>
          <ScrollView
            style={{ flex: 1, }}
            alwaysBounceVertical={false}
            showsVerticalScrollIndicator={false}>
            {
              sections.map((item, index) => (
                <TouchableOpacity key={index}
                  activeOpacity={1}
                  style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, width: 120, backgroundColor: selectedSection?.sectionId === item.sectionId ? 'white' : theme.grey1, borderRadius: 5, margin: 5 }}
                  onPress={() => this.doPressSection(item)}>
                  {
                    item.orderType === 'B' &&
                    <Text style={{ fontFamily: 'varela-round', fontSize: 12, textAlign: 'center', color: theme.paid, textTransform: 'uppercase' }}>{intl.get('takeaway')}</Text>
                  }
                  <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: selectedSection?.sectionId === item.sectionId ? 'black' : theme.grey9 }}>{item.name}</Text>
                </TouchableOpacity>
              ))
            }
          </ScrollView>
        </View>

      </View >
    );
  }

}

const styles = StyleSheet.create({
  textInput: {
    color: 'white',
    height: 60,
    borderRadius: 30,
    width: '90%',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: 120,
    textAlign: 'center',
    fontFamily: 'varela-round',
    fontSize: 24,
  },
  onetwo: {
    position: 'absolute', top: '0', left: '0', height: '12.5%', width: '12.5%',
  },
  tableLowerBand: {
    fontFamily: 'varela-round',
    width: '100%',
    paddingVertical: 5,
    backgroundColor: theme.black8,
    justifyContent: 'center',
    alignItems: 'center',
    color: 'white',
    fontSize: 12,
    textAlign: 'center',
    textTransform: 'uppercase',
  },
});
